.master {
    padding-top: 12.5rem;
    padding-bottom: 7rem;

    @include media(768px) {
        padding-top: 5rem;
        padding-bottom: 2rem;
    }

    &__wrapper {
        width: 100%;
        position: relative;
        max-width: 101.7rem;
        margin: 0 auto;

        .swiper-navigation {
            @include media(1050px) {
                display: none;
            }
        }
    }

    /* .master__title */

    &__title {
        text-align: center;
        margin-bottom: 4rem;
    }

    /* .master__slider */

    &__slider {
        width: 100%;
        height: 100%;
        filter: drop-shadow(0 7px 7px rgba(0, 0, 0, 0.3));
        z-index: 2;
        position: relative;
        padding-bottom: 5rem;

        .swiper-pagination-bullets .swiper-pagination-bullet {
            margin: 0 0.5rem;
        }

        @include media(960px) {
            max-width: 65.8rem;
        }
        @include media(768px) {
            max-width: 35.4rem;
            padding: 0 2rem 5rem;
        }
        @include media(425px) {
            max-width: 35.4rem;
            padding: 0;
            padding-bottom: 5rem;
        }
    }

    /* .master__item */

    &__item {
        width: 100%;
        max-width: 31.4rem;
        border-radius: 1.5rem;
        background-color: #fff;
        padding: 2.5rem;
        padding-top: 0;
    }

    /* .master__box-img */

    &__box-img {
        width: 100%;
        height: 24.2rem;
        max-width: 24.2rem;
        margin: 0 auto;
        margin-bottom: 1rem;
        border-radius: 1.8rem;
        overflow: hidden;
    }

    /* .master__item-name */

    &__item-name {
        font-family: "TrajanPro3";
        font-weight: 400;
        text-align: center;
        margin-bottom: 1rem;
    }

    /* .master__item-position */

    &__item-position {
        margin-bottom: 1.5rem;
        text-align: center;
        font-size: 1.6rem;
    }

    /* .master__item-btn */

    &__item-btn {
        max-width: 13.5rem;
        height: 4rem;
        font-size: 1.1rem;
    }
}