.atmosphere {
    padding: 8rem 0;

    @include media(768px) {
        padding: 4rem 0;
    }

    /* .atmosphere__title */

    &__title {
        text-align: center;
        margin-bottom: 9rem;

        @include media(960px) {
            margin-bottom: 5rem;
        }
    }

    /* .atmosphere__wrapper */

    &__wrapper {
        width: 100%;
        max-width: 108rem;
        position: relative;
        margin: 0 auto;

        .swiper-navigation .atmosphere__next,
        .swiper-navigation .atmosphere__prew {
            top: 35%;
        }

        .swiper-navigation {
            @include media(1050px) {
                display: none;
            }
        }
    }

    /* .atmosphere__slider */

    &__slider {
        z-index: 2;
        margin-bottom: 1.5rem;

        @include media(960px) {
            max-width: 52.2rem;
        }

        @include media(425px) {
            margin-bottom: .5rem;
        }
    }

    /* .atmosphere__min-slider */

    &__min-slider {
        width: 100%;
        max-width: 68rem;
        z-index: 2;
    }

    /* .atmosphere__modal */

    &__modal {
        width: 100%;
        max-width: 52rem;
        height: 36.5rem;
        display: block;
        overflow: hidden;
        border: 0.3rem solid transparent;
        transition: all 0.24s;

        &:hover,
        &:focus-visible {
            border: 0.3rem solid $accent;

            .atmosphere__img {
                transform: scale(1.1);
            }
        }

        @include media(768px) {
            height: 21.8rem;
         }
    }

    /* .atmosphere__min-item */

    &__min-item {
        width: 100%;
        max-width: 14rem;
        height: 9rem;
        overflow: hidden;
        cursor: pointer;

        @include media(768px) {
            height: 6rem;
         }
    }

    &__modal,
    &__min-item {
        border-radius: 1.5rem;

        @include media(768px) {
            border-radius: 1rem;
         }
    }

    /* .atmosphere__img */

    &__img {
        transition: transform 2s;
    }
}